@import url(https://fonts.googleapis.com/css2?family=VT323);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.mainTheme{font-family:'VT323', monospace;background-color:red;color:#fff;width:100%;min-height:100vh;display:flex;justify-content:center}@media only screen and (min-width: 600px){.mainTheme{background-color:#090}}.startTheme{font-family:'VT323', monospace;background-color:blue;color:#fff;width:100%;min-height:100vh;display:flex;justify-content:center}.innerBox{align-self:center;flex-wrap:nowrap;border:1px solid #fff;padding:10px}.alertMsg{background-color:#ff0;color:#000}

